.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* CSS FOR DICE ROLLER FUNCTION */

/* Dice label container for positioning labels */
.dice-label-container {
  display: flex;
  justify-content: space-between;
  width: 250px;
  margin: 0 auto;
  margin-bottom: 5px;
}

/* Label styles for Attacker and Defender */
.dice-label-left, .dice-label-right {
  font-size: 1.2rem;
  font-weight: bold;
}

.label {
  text-align: center;
}

/* General dice CSS */
.dice-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.dice {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin: 0 10px;
  background-color: #f0f0f0;
  border: 2px solid #000;
  border-radius: 12px;
}

.dice-value {
  font-size: 3rem;
  font-weight: bold;
}

/* Dropdown container */
.dropdown-container {
  display: flex;
  justify-content: space-between;
  width: 360px;
  height: 100px;
  margin: 0 auto;
}

.dropdown-left, .dropdown-right {
  width: 160px;
}

label {
  display: block;
  font-size: 1rem;
  margin-bottom: 5px;
}

select {
  width: 100%;
  padding: 5px;
  font-size: 1rem;
  border: 2px solid black;
  border-radius: 6px;
}

.hits-display {
  margin-bottom: 1rem;
}

.card:hover {
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.3s ease;
}

.card {
  width: 85%; 
  max-width: 700px;
  margin: auto;
}

.faction-select {
  width: 100%;
  max-width: 300px;
  border: 1px solid #ccc;
  padding: 0.5rem;
  border-radius: 4px;
}


/* Font specifics */

.baskerville {
  font-family: 'Baskerville', serif;
}

@import url('https://fonts.googleapis.com/css2?family=Luminari&family=Mrs+Eaves&display=swap');

@font-face {
  font-family: 'Luminari';
  src: url('./fonts/Luminari-Regular.ttf') format('truetype');
}

.luminari-font {
  font-family: 'Luminari', serif;
}

.mrs-eaves-font {
  font-family: 'Mrs Eaves', serif;
}


